<template>
	<div class="main">
		<div v-wechat-title="$route.meta.title"></div>
		<!-- 修改手机号 -->
		<div class="back" @click="goBack()">
			<img src="../img/left.png" alt="">
			<span>返回</span>
		</div>
		<div class="userInfo">
			<span>
				旧手机号
			</span>
			<div class="userPhone">
				<img src="../img/phone.png" alt="">
				<input  type="number" placeholder="请输入旧手机号" v-model="oldPhone">
			</div>
			<span class="none">
				新手机号
			</span>
			<div class="userPhone">
				<img src="../img/phone.png" alt="">
				<input type="text" placeholder="请输入新手机号" v-model="newPhone">
				<p class="yzm" v-if="isVerify" @click="yzm()">{{yzmContent}}</p>
				<p class="yzm" v-if="!isVerify">{{yzmContent}}</p>
			</div>
			<span class="none">
				短信验证码
			</span>
			<div class="password">
				<img src="../img/password.png" alt="">
				<input type="text" placeholder="请输入短信验证码" v-model="verifyCode">
			</div>
			<div class="reg" >
				<button @click="edit()">确认修改</button>
			</div>

		</div>
	</div>

</template>

<script>
	import request from '../../../../static/js/request';
    import request1 from '../../../../static/js/request1';
	import result from '../../../../static/js/result1';
	export default {
		data() {
			return {
				oldPhone:'',
				newPhone:'',
				verifyCode:'',
				isVerify:true,
				yzmContent:'获取验证码'
			}
		},
		mounted() {

		},
		created() {

		},
		methods: {
			goBack(){
				this.$router.go(-1)
			},
			async yzm(){
				if(!this.newPhone){
					weui.alert('请输入新手机号')
					return false
				}
				let params = {
					str:this.newPhone
				}
				let submitInfo = await request( '/api/open/sms/sendSms',
						'post', params, 'application/x-www-form-urlencoded')
				// loading.hide()
				let info = result(submitInfo)
				if( info.code==500 ){
					weui.alert('请输入正确验证码');
					return  false;
				}
				// loading.hide()
				if (!info) return;
				weui.toast('已发送');
				let time = 60;
				let timer = setInterval(() => {
					if(time == 0) {
						clearInterval(timer);
						this.isVerify = true;
						this.yzmContent = "获取验证码";
						return false;
					} else if(time > 0){
						this.yzmContent =time + '秒后重试';
						this.isVerify = false;
						time--
					}
				},1000)
				console.log(info)
			},
			 edit(){
				let url=''
				if(this.$store.state.role==2){
					url='/api/token/user/updateUserStudentPhone'
				}else if(this.$store.state.role==3){
					url='/api/token/user/updateUserParentPhone'
				}
			 	let _this = this;
				if(_this.oldPhone==''){
					weui.alert("请输入旧手机号！");
				}
				if(_this.newPhone==''){
					weui.alert("请输入新手机号！");
				}
				if(_this.verifyCode==''){
					weui.alert("请输入短信验证码！");
				}
				weui.dialog({
					title: '确认修改手机号吗',
					content: '将手机号修改为'+_this.newPhone,
					className: 'custom-classname',
					buttons: [{
						label: '取消',
						type: 'default',
						onClick: function () {
							weui.toast('已取消修改', 2000);
						}
					}, {
						label: '确定',
						type: 'primary',
						onClick: async  function () {
							let params = {
								access_token:_this.$store.state.token,
								oldPhone:_this.oldPhone,
								newPhone:_this.newPhone,
								verifyCode:_this.verifyCode
							}
							let loading = weui.loading('loading', {
								className: 'custom-classname'
							});
							let edit = await request(url,
									'post', params, 'application/x-www-form-urlencoded')
							let res = result(edit)
							loading.hide()
							if(!res) return;
							console.log('结果',edit)
							this.isVerify = true;
							localStorage['edit'] = true;
							weui.toast('修改成功', 2000);
							_this.$router.go(-1)
						}
					}]
				});

			},

		},
		watch: {



		}
	}
</script>

